:root {
    --blue: #1e90ff;
    --white: #fffffa;
    --black: #0C0C0C;
}

*{
    user-select: none;
}

.contentbox{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    
    width: 100%;
    margin-top: 8vh;
}

.content{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background-color: #0c0c0c00;
    flex-direction: column;
    width: 90%;
    aspect-ratio: 16/7.2;
    /*height: 80vh;*/
    margin-top: 11.5vw;
    margin-bottom: 10vw;
    z-index: 2;
    border-radius: 1vw;
}

.content .header{
    width: 100%;
    height: 50%;
    background-color: rgba(0, 0, 255, 0);
    color: var(--black);
    font-family: Heavy;
    font-size: 8vw;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.content .text{
    position: relative;
    width: 100%;
    height: 50%;
    background-color: rgba(255, 0, 0, 0);
    color: var(--black);
    font-family: Heavy;
    font-size: 3vw;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: center;
    align-items: center;
}

.buttons1{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 0, 0, 0);
    width: 100%;
    height: 40%;
    left: 0;
    bottom: 0;
}

.button1{
    display: flex;
    height: 50%;
    width: fit-content;
    background-color: rgb(0, 0, 0);
    color: white;
    border-radius: 1vw;
    padding: 1%;
    margin-left: 1vw;
    margin-right: 1vw;
    transition: all 0.3s ease;
    cursor: pointer;
}

#discord:hover{
    color: #5865F2;
}

#cv:hover{
    color: #ED4245;
}

#github:hover{
    color: #2dba4e;
}


.text .expand{
    position: absolute;
    left: 0;
    top: 30%;
    display: flex;
    width: 100%;
    background-color: rgba(178, 34, 34, 0);
}

.expand .expand-left{
    display: flex;
    justify-content: flex-start;
    width: 50%;
}

.expand .expand-right{
    display: flex;
    justify-content: flex-end;
    width: 50%;
}

.Collapsible__trigger{
    background-color: rgba(0, 0, 0, 0);
    color: var(--black);
    cursor: pointer;
}

.Collapsible__contentOuter{
    background-color: rgba(231, 231, 231, 0.5);
    box-shadow: -0.5vw 1.0vw 1vw #00000036;
    color: white;
    border-radius: 2%;
}

.expand .Collapsible{
    margin-top: 10%;
    width: 100%;
}

.Collapsible__contentInner{
    background-color: rgba(255, 0, 0, 0);
    margin-left: 0.5vw;
    color: #0C0C0C;
}

.programinglaunguages{
    width: 90%;
    background-color: rgba(178, 34, 34, 0);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.programinglaunguages li{
    font-size: 2vw;
    margin-bottom: 1vw;
    user-select:text;
}

#land{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 92vh;
    background-color: rgba(255, 0, 0, 0);
    font-family: Heavy;
}

#land .l1{
    font-size: 14vw;
}