.contactscreen{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 0, 0, 0);
}

.form{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 1vw;
    width: 50vw;
    height: 20vw;
    background-color: #f3f3f3;
    box-shadow: -0.5vw 1.0vw 1vw #00000036;
    z-index: 2;
    border-radius: 1vw;
}

.form .email{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 25%;
    background-color: rgba(255, 217, 0, 0);
}

.form .message{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 60%;
    background-color: rgba(255, 105, 180, 0);
}

.form .button{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 15%;
    background-color: rgba(105, 255, 168, 0);
}

.form .form-header{
    color: var(--black);
    font-family: Heavy;
    font-size: 1.5vw;
    width: 100%;
    height: 40%;
    text-align: center;
}

.input{
    width: 80%;
    height: 30%;
}

.input{
    color: var(--black);
    font-size: 1vw;
    font-family: Heavy;
    background-color: #e0e0e0;
    border: solid 0.1vw rgba(255, 255, 255, 0.226);
    border-radius: 0.5vw;
}

#emailinput{
    text-align: center;
}

.wrong{
    color: red;
    font-size: 0.75vw;
    font-family: Heavy;
}

#messageheader{
    height: 20%;
}

#messageinput{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    text-align: start;
    height: 65%;
}

#submit{
    width: 80%;
    height: 80%;
    border-radius: 1vw;
    font-size: 1vw;
    font-family: Heavy;
    background-color: black;
    color: var(--white);
    border: solid 0.1vw rgba(255, 255, 0, 0);
    transition: all 0.5s ease;
}

#submit:hover{
    color:yellow
}

#success{
    position: fixed;
    color: rgb(0, 194, 0);
    font-size: 3vw;
    font-family: Heavy;
    overflow: hidden;
}