@import url('https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap');
@font-face {
    font-family: Poetsen;
    src: url("./assets/fonts/PoetsenOne-Regular.ttf") format("truetype");
}
@font-face {
    font-family: Heavy;
    src: url("./assets/fonts/Mont-Heavy.ttf") format("truetype");
}
@font-face {
    font-family: Lucky;
    src: url("./assets/fonts/LuckiestGuy-Regular.ttf") format("truetype");
}
.upperbar{
    display: flex;
    flex-direction: row;
    position: fixed;
    width: 100%;
    height: 8vh;
    /*background-color: #0C0C0C;
    background: linear-gradient(0deg, rgba(0,0,0,0.9206057422969187) 0%, rgba(0,0,0,0.9822303921568627) 50%, rgba(3,0,41,1) 100%);*/
    background-color: #FFFAFA;
    z-index: 3;
}

.upperbar .leftpart{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 100%;
    width: 50%;
}

.upperbar .rightpart{
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
    width: 75%;
    background-color: rgba(255, 250, 240, 0);
}

.upperbar .rightpart a{
    font-family: Heavy;
    font-size: 1.5vw;
    color: #0C0C0C;
    text-decoration: none;
    margin-left: 1vw;
    margin-right: 1vw;
    transition: all 0.8s ease;
    cursor: pointer;
}

a:hover {
    transform: scale(110%);
}

#name{
    color: #0C0C0C;
    text-align: center;
    font-size: 2vw;
    font-family: Heavy;
    margin-left: 1vw;
}

