@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.contact{
  position: fixed;
  width: 5vw;
  height: 5vw;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("assets/images/bubble.png");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  right: 1vw;
  bottom: 1vw;
  border-radius: 0%;
  animation: hover 5s ease infinite;
  transition: all 0.8s ease;
}

.contact:hover{
  animation: none;
  cursor: pointer;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes zoom {
  0%{
      transform: scale(1);
  }
  100%{
      transform: scale(1.5);
  }
}

@keyframes hover{
  0%{
    transform: translate(0vw, 0vw) rotate(0deg);
  }
  25%{
    transform: translate(-0.25vw, 0vw) rotate(5deg);
  }
  50%{
    transform: translate(-0.25vw, 0.25vw) rotate(10deg);
  }
  75%{
    transform: translate(-0.25vw, 0vw) rotate(5deg);
  }
  100%{
    transform: translate(0vw, 0vw) rotate(0deg);
  }
}
